export default {
  props: ["reason","orderId","reload","changeVisibileModal","cancelUrl"],
  data() { 
	  return {
		  chooseData: null,
		  detail: '',
		  pictures: [],
		  static:ut.static,
		  phone:''
	  }
  },
  methods: {
	  choose(data){
			this.chooseData = data;  
	  },
		afterSale() {
			let pictures='';
			this.pictures.forEach(item=>{
				if(pictures){
					pictures+=','+item
				}else{
					pictures+=item
				}
				
			})
			ut.request({
				data: {
					orderId: this.orderId,
					reasonId: this.chooseData,
					detail: this.detail,
					phone:this.phone,
					pictures:pictures
				},
				url: 'service/order/afterSale'
			}).then(data=>{
				this.$parent.changeVisibileModal(false)
				this.$emit('reload');
				ut.totast("申请成功")
			})
		},
	  sub() {
			if(!this.chooseData){
				ut.totast("请选择售后原因")
				return;
			}
			if(!this.detail){
				ut.totast("请输入详情")
				return;
			}
			if(!ut.checkmobile(this.phone)){
				ut.totast("请输入正确的手机号")
				return;
			}
			this.afterSale();
	  },
	  uploadimg(){
	  	ut.uploadimg({
	  		callback:res=>{
	  			this.req_uploadimg(res);
	  			
	  		}
	  	})
	  },
	  req_uploadimg(res){
	  	const that=this;
	  	wx.uploadFile({
	  		url: ut.uploadimgurl+"common/imageUpload", //仅为示例，非真实的接口地址
	  		filePath: res,
	  		name: 'picture',
	  		header: {
	  			"Accept": "*/*"
	  		},
	  		success(res) {
	  			console.log(res)
	  			if(!res.data){
	  				ut.totast('上传图片失败')
	  				return;
	  			}
	  			res.data=JSON.parse(res.data);
	  			if(res.data.code==0){
	  				that.pictures.push(res.data.data);
	  			}else{
	  				ut.totast('上传图片失败')
	  			}
	  		}
	  	})
	  }
  }
}
