export default {
  props: ["orderId","reload","changeVisibileModal","confirmPlanlist"],
  data() { 
	return {

	}
  },
  methods: { 
	 cancel_order() {
	 			
		ut.request({
			data: {
				orderId: this.orderId,
			},
			url: "service/order/cancelOrder"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("订单取消成功")
		})
	},
	 cancel_confirm() {
		 this.$parent.changeVisibileModal(false)
		this.$emit('changeb',true)
	 },
	againPlan() {
		ut.request({
			data: {
				orderId: this.orderId
			},
			url: "service/order/againPrice"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("操作成功")
			console.log(data)
		})
	},
	confirmPlan() {
		ut.request({
			data: {
				orderId: this.orderId
			},
			url: "service/order/confirmPrice"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("操作成功")
			console.log(data)
		})
	}
  }
}
